const KEY = "chat-data";
var path = require('path');

if(!localStorage.getItem(KEY)) {
    let now = new Date();
    let data = {
        user: {
            id: 1,
            name: '',
            avatar: './dist/images/1.jpg'
        },
        //
        userList: [
            {
                id: 2,
                name: '',
                avatar: './dist/images/2.png'
            },
            {
                id: 3,
                name: 'webpack',
                avatar: './dist/images/3.jpg'
            }
        ],
        sessionList: [
            {
                id: 2,
                messages: [
                    {
                        text: '',
                        date: now,
                        self: false //user
                    },
                    {
                        text: '',
                        date: now,
                        self: false
                    }
                ]
            },
            {
                id: 3,
                messages: [
                    {
                        text: '',
                        date: now,
                        self: false
                    }
                ]
            }
        ]
    };

    localStorage.setItem(KEY, JSON.stringify(data));
}

export default {
    fetch() {
        return JSON.parse(localStorage.getItem(KEY));//js
    },
    //data
    save(data) {
        localStorage.setItem(KEY, JSON.stringify(data));
    }
};


